'use client';

import Sidebar from './SideBar';
import Profile from './Profile';
import Search from './Search';
import TotalSales from './TotalSales';
import TotalProducts from './TotalProducts';
import TotalSellers from './TotalSellers';
import TotalOrders from './TotalOrders';
import RecentSellerMessages from './RecentSellerMessages';
import '../styles/styles-dashboard.css'

const Dashboard = () => {
  return (
<div className="relative h-screen">
  <Sidebar className="fixed top-0 left-0 h-full w-full md:w-1/4 bg-blue-950 text-white p-6 z-10" />

  {/* Main content */}
  <div className="ml-0 md:ml-1/4 flex-1 p-6 bg-gray-100 relative">
    {/* Top bar */}
    <div className="absolute top-0 left-0 w-full flex justify-between items-center px-4 md:px-6 z-20">
      <div className="flex-grow">
        <Search />
      </div>
      <div className="absolute right-0 top-0 md:right-4">
        <Profile />
      </div>
    </div>

    {/* Main dashboard content */}
    <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mt-24 mb-6">
      <TotalSales />
      <TotalProducts />
      <TotalSellers />
      <TotalOrders />
    </div>

    <div className="mb-6">
      <RecentSellerMessages />
    </div>
  </div>
</div>

  );
};

export default Dashboard;
